

































.new-widget {
  padding: 16px;
  border: 1px solid #ddd;
  border-radius: 8px;
  text-align: center;
}

.new-widget h3 {
  margin: 0 0 8px;
  font-size: 18px;
  color: #333;
}

.new-widget p {
  margin: 0 0 16px;
  font-size: 14px;
  color: #666;
}

.new-widget button {
  padding: 8px 16px;
  font-size: 14px;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.new-widget button:hover {
  background-color: #0056b3;
}
